@import url(//maxcdn.bootstrapcdn.com/font-awesome/4.3.0/css/font-awesome.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}






section {
    margin-top: 4rem;
}

h1 {
    font-size: 65px;
    font-style: oblique;
    font-family: 'Kaushan Script', cursive;
}

.about-me {
    background: rgba(255,255,255, .6);
    width: 35rem;
    height: 35rem;
    margin: 0 auto;
    border-radius: 50%;
    display: grid;
    place-items: center;
    box-shadow: 2px 3px 3px 4px #000000;
}

.clu-img {
    height: 15vh;
    width: auto;
    border-radius: 100%;
    box-shadow: 2px 3px 3px 4px #000000;
    margin-top: -11rem;
}

.about-text {
    width: 67.5%;
    margin-top: -11.5rem;
    text-align: center;
}

@media screen and (max-width: 600px) {
    .about-me {
        width: 25rem;
        height: 25rem;
    }
    .about-title {
        font-size: 50px;
    }
    .clu-img {
        height: 5vh;
        margin-top: -11rem;
    }
}
section {
    margin-top: 4rem;
}

.contact-title {
    font-size: 85px;
    font-style: oblique;
    font-family: 'Kaushan Script', cursive;
}

.contact-me {
    background: rgba(255,255,255, .6);
    width: 35rem;
    height: 35rem;
    margin: 0 auto;
    border-radius: 50%;
    display: grid;
    place-items: center;
    box-shadow: 2px 3px 3px 4px #000000;
}

form {
    width: 67.5%;
    margin-top: -11.5rem;
    text-align: center;
}
label {
    font-size: 18px;
    padding: 5px;
    font-weight: 700;
}

input {
    border-radius: 15px;
    width: 22rem;
    height: 2rem;
    border: 0 0 0 0.9;
    box-shadow: .5px .75px .75px 1px #000000;
}
input:focus {
    outline: none;
    border-style: solid;
    border-color: lightskyblue;
}

.message-input {
    height: 4rem;
}
.error-text {
    margin-top: 5px;
    font-size: 18px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 5px;
}

.form-btn {
    margin-top: 10px;
    border-radius: 32px;
    width: 10rem;
    background: rgba(255, 255, 255, .9);
    box-shadow: 2px 3px 3px 4px #000000;
    font-size: 18px;
    font-weight: 600;
    color: black;
}

.form-btn:hover {
    box-shadow: 8px 8px 16px rgba(0, 0, 0, 0.8);
}
.form-btn:active {
    box-shadow: inset -4px -4px 8px rgba(255, 255, 255, 0.5),
    inset 8px 8px 16px rgba(0, 0, 0, 0.1);
}
.form-btn:focus {
    outline: none;
}

@media screen and (max-width: 600px) {
    .contact-me {
        width: 25rem;
        height: 25rem;
    }

    .contact-title {
        font-size: 45px;
        margin-bottom: 5rem;
    }
    input {
        width: 18rem;
    }
    .error-text {
        margin-bottom: 2.5px;
    }
    .form-btn {
        margin-top: 5px;
    }
}
.modal-btn {
    color: black;
    background: white;
}
.project-div {
  padding-bottom: 15vh;
}

.project-img {
  margin-right: calc(1rem * 0.5);
  margin-left: calc(1rem * 0.5);
}
.test {
  display :inline-block;
  text-align: center;
  width: 50%;
  padding-left: 5vh;
  padding-right: 5vh;
}

.img-thumbnail {
  width: 100%;
  height: 700px;
  margin-top: 50px;
  margin-bottom: 35px;
  cursor: pointer;
  border: seashell;
  border-radius: 5%;
  box-shadow: 2px 3px 3px 4px #000000;

}

.modal-backdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: none;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modal-container {
	background: rgba(0, 0, 0, 0.4);
	border-radius: 5px;
	max-width: 65%;
	margin: 10px auto;
	padding: 15px;
	width: 960px;
  border-radius: 5%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

.modal-title {
	font-size: 28px;
	font-weight: 600;
	margin-bottom: 25px;
  color: white;
}

.modal-desc {
  font-size: 25px;
  color: white;
  margin: 5px;
  width: 100%;
}

button {
	background: lightslategray;
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 26px;
	font-weight: 600;
	margin-bottom: 70px;
	padding: 10px 15px;
}

.modal-project {
  height: 80vh;
  max-width: 95%;
  border-radius: 2.5%;
}

.dest-links {
  display: block;
  font-family: 'Kaushan Script', cursive;
  font-size: 35px;
  color: black;
  font-weight: 900;
  /* text-decoration: underline; */
  border-radius: 50%;
  box-shadow: 2px 3px 3px 4px #000000;
  background: whitesmoke;
}

.dest-links:hover {
    background-image: url(https://media.giphy.com/media/izbqy36DBmhTBLEpgJ/giphy.gif);
    background-size: cover;
    text-shadow: 2px 2px #fff000;
}

@media screen and (max-width: 1700px) {
  .img-thumbnail{
    height: 400px;
  };
}

@media screen and (max-width: 900px) {
  .modal-project {
    height: auto;
  }
  .img-thumbnail {
    height: 300px;
  }
}

@media screen and (max-width: 800px) {
  .img-thumbnail {
    height: auto;
  }
  .test {
    width: 100%;
  }
}
