.project-div {
  padding-bottom: 15vh;
}

.project-img {
  margin-right: calc(1rem * 0.5);
  margin-left: calc(1rem * 0.5);
}
.test {
  display :inline-block;
  text-align: center;
  width: 50%;
  padding-left: 5vh;
  padding-right: 5vh;
}

.img-thumbnail {
  width: 100%;
  height: 700px;
  margin-top: 50px;
  margin-bottom: 35px;
  cursor: pointer;
  border: seashell;
  border-radius: 5%;
  box-shadow: 2px 3px 3px 4px #000000;

}

.modal-backdrop {
	background: rgba(0, 0, 0, .65);
	bottom: 0;
	left: 0;
	overflow: none;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 1;
}

.modal-container {
	background: rgba(0, 0, 0, 0.4);
	border-radius: 5px;
	max-width: 65%;
	margin: 10px auto;
	padding: 15px;
	width: 960px;
  border-radius: 5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.modal-title {
	font-size: 28px;
	font-weight: 600;
	margin-bottom: 25px;
  color: white;
}

.modal-desc {
  font-size: 25px;
  color: white;
  margin: 5px;
  width: 100%;
}

button {
	background: lightslategray;
	border: 0;
	border-radius: 5px;
	color: #fff;
	cursor: pointer;
	font-size: 26px;
	font-weight: 600;
	margin-bottom: 70px;
	padding: 10px 15px;
}

.modal-project {
  height: 80vh;
  max-width: 95%;
  border-radius: 2.5%;
}

.dest-links {
  display: block;
  font-family: 'Kaushan Script', cursive;
  font-size: 35px;
  color: black;
  font-weight: 900;
  /* text-decoration: underline; */
  border-radius: 50%;
  box-shadow: 2px 3px 3px 4px #000000;
  background: whitesmoke;
}

.dest-links:hover {
    background-image: url(https://media.giphy.com/media/izbqy36DBmhTBLEpgJ/giphy.gif);
    background-size: cover;
    text-shadow: 2px 2px #fff000;
}

@media screen and (max-width: 1700px) {
  .img-thumbnail{
    height: 400px;
  };
}

@media screen and (max-width: 900px) {
  .modal-project {
    height: auto;
  }
  .img-thumbnail {
    height: 300px;
  }
}

@media screen and (max-width: 800px) {
  .img-thumbnail {
    height: auto;
  }
  .test {
    width: 100%;
  }
}